<template>
  <transition name="el-zoom-in-center">
    <div id="app" v-show="cardshowflag">
      <div class="mainpage" ref="mainpage">
        <HelloWorld :msg="perinfo" />
        <card1 />
        <card2 />
      </div>

      <perinfo class="animated" :class="peranimate[$store.state.perinfoflag]" />
    </div>
  </transition>
</template>

<script>
//class="animated fadeInLeft"
import HelloWorld from "./components/HelloWorld.vue";
import card1 from "./components/card1";
import card2 from "./components/card2";
import perinfo from "./components/personinfo";
export default {
  name: "app",
  components: {
    HelloWorld,
    card1,
    card2,
    perinfo
  },
  data() {
    return {
      perinfo: { name: "刘欢" },
      cardshowflag: false,
      peranimate: { true: " jackInTheBox", false: "rollOut" }
    };
  },
  methods: {
    changeflag() {
      this.cardshowflag = true;
    },
    changeblur(n) {
      if (n === true) {
        this.$refs.mainpage.classList.add("bluractive");
      } else if (n === false) {
        this.$refs.mainpage.classList.remove("bluractive");
      }
    }
  },
  computed: {
    linstenstate() {
      return this.$store.state.perinfoflag;
    }
  },
  watch: {
    linstenstate: function(n, o) {
      this.changeblur(n);
    }
  },
  mounted() {
    this.changeflag();
    // setInterval(() => {
    //   this.$store.commit("showperinfofunc");
    // }, 20000);
  }
};
</script>

