<script>
import { constants } from "crypto";
export default {
  name: "HelloWorld",
  props: {
    msg: String
  },
  data() {
    return {
      n1: 0,
      n2: 0,
      n3: 0,
      n4: 0
    };
  },
  methods: {
    setprogress(y, n) {
      let sn = setInterval(() => {
        y += 1;
        if (y === n) {
          clearInterval(sn);
        }
        if (n === 85) {
          this.n1 = y;
        } else if (n === 75) {
          this.n2 = y;
        } else if (n === 80) {
          this.n3 = y;
        } else if (n === 70) {
          this.n4 = y;
        }
      }, 0);
    }
  },
  mounted() {
    setTimeout(() => {
      this.setprogress(this.n1, 85);
      this.setprogress(this.n2, 75);
      this.setprogress(this.n3, 80);
      this.setprogress(this.n4, 70);
    }, 500);
  }
};
</script>

